<template>
<div class="newTitle" style="z-index: 2" v-show="showGuild" :class="{fourTab: fourTab}">
  <div class="top-lead" :class="{isApp: $config.source_style >= 6}" v-if="guide" @click="toggleSHOW(1)">
    <div class="conts f24 text-center">每30分钟就有金币领</div>
  </div>
  <div class="bottom-lead" @click="toggleSHOW(2)" :class="{isNew: commonState.userInfo.is_new === 1, 'tabHide': tabHide}" v-if="!guide && dowmGuide && this.commonState.hasRed['/task']">
    <div class="conts f24 text-center" v-if="commonState.userInfo.is_new === 1  && this.commonState.hasRed['/task']">连续签到7天得{{Math.round(368 * commonState.inviteVcoin)}}金币</div>
    <div class="conts f24 text-center" v-else>今日奖励可领取</div>
  </div>
</div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  computed: mapState({
    commonState: state => state.common
  }),
  props: {
    newUserClick: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      guide: true,
      dowmGuide: true,
      fourTab: false,
      tabHide: false,
      showGuild: false,
      tempArr: [
        {txt: '首页', to: '/', img1: require('../../assets/img/footerImg3.png'), img2: require('../../assets/img/footerImg1.png')},
        {txt: '影视', to: '/movie', img1: require('../../assets/img/movie/movie.png'), img2: require('../../assets/img/movie/movie_active.png')},
        {txt: '赚车赚房', to: '/make_money', img1: require('../../assets/img/make_money.png'), img2: require('../../assets/img/make_money_active.png')},
        {txt: '任务', to: '/task', img1: require('../../assets/img/task.png'), img2: require('../../assets/img/task_active.png')},
        {txt: '我的', to: '/personal_center', img1: require('../../assets/img/footerImg2.png'), img2: require('../../assets/img/footerImg4.png')}
      ]
    }
  },
  mounted () {
    if (this.commonState.userInfo.is_new === 0) {
      this.toggleSHOW()
    }
  },
  watch: {
    'newUserClick' () {
      this.toggleSHOW(1)
    }
  },
  methods: {
    outVanish () {
      this.$emit('change')
    },
    toggleSHOW (num) {
      if (num === 1 || num === undefined) {
        if (this.$config.source_style >= 6) {
          if (localStorage.getItem('menu')) {
            let temp = JSON.parse(localStorage.getItem('menu')).arr
            let arr = []
            for (let v of temp) {
              if (v.show === 1) {
                arr.push(v)
              }
            }
            if (arr.length === 4) {
              this.fourTab = true
            }
          } else {

          }
          this.guide = false
        } else {
          this.guide = false
        }
      } else if (num === 2) {
        this.dowmGuide = false
        this.$router.replace('/task')
      }
      this.$utils.cache('firstSHOW', 1)
    },
    setTabHide (arr) {
      if (arr[1].show === 0) {
        this.tabHide = true
      } else {
        this.tabHide = false
      }
      this.showGuild = true
    }
  },
  created () {
    this.tabHide = false
    this.showGuild = true
  }
}
</script>

<style lang="scss" scoped>
  .newTitle{
    position: fixed;
  }
  .top-lead{
    position: fixed;
    top: 80px;
    width: 260px;
    height: 60px;
    border: 60px;
    line-height: 60px;
    border-radius: 15px;
    right: 30px;
    background: #565656;
    color: #fff;
    &.isApp {
      right: 98px;
    }
    .fxts{
      width: 28px;
      height: 28px;
      background: url('../../assets/img/fxts.png') no-repeat;
      background-size: 100%;
      display: inline-block;
      vertical-align: middle;
      margin-left: 20px;
    }
    &:before{
      position: absolute;
      content: '';
      z-index: 12;
      width: 0;
      height: 0;
      line-height: 0;
      font-size: 0;
      border-width:0 14px 14px;
      border-style: solid;
      border-color:transparent  transparent #565656 transparent;
      top:-12px;
      right: 30px;
    }
  }
  .fourTab {
    .bottom-lead {
      right: 11%;
      &.isNew {
        right: 3%;
      }
    }
  }
  .bottom-lead{
    position: fixed;
    bottom: 120px;
    min-width: 260px;
    box-sizing: border-box;
    padding: 0 20px;
    height: 60px;
    border: 60px;
    line-height: 60px;
    border-radius: 15px;
    transform: translateX(-50%);
    color: #fff;
    background: #565656;
    margin: 0;
    right: 1%;
    &.isNew {
      right: -6.5%;
    }
    
    .fxts{
      width: 28px;
      height: 28px;
      background: url('../../assets/img/fxts.png') no-repeat;
      background-size: 100%;
      display: inline-block;
      vertical-align: middle;
      margin-left: 20px;
    }
    &:before{
      position: absolute;
      content: '';
      z-index: 2;
      width: 0;
      height: 0;
      line-height: 0;
      font-size: 0;
      border-width: 15px;
      border-style: solid;
      border-color: #565656 transparent transparent transparent;
      bottom:-28px;
      left: 174px;
    }
  }
</style>



