<template>
  <div id="commonList">
    <div class="wrapper" ref="wrapper" :class="{isBrowser: $config.source_style < 6}">
      <div class="bg-fff" ref="bg_fff">
        <transition name="topTxt">
          <div class="text-center lh66 topTxt" v-if="topBol">
            {{num!==0?"为您推荐"+num+"条更新":"暂无更新，请休息会再来~"}}
          </div>
        </transition>
        <news-list ref="muAdRooot" :hasBottom="1" @deleteAd="deleteAd" :loading="loading" :column_id="columnid" @refresh="refresh" :list="topList.concat(list)" @upDate="upDate"></news-list>
      </div>
    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
import NewsList from '../common/news-list'
export default {
  name: 'commonList',
  props: {
    columnid: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      setBrowser: {},
      list: [],                   // 新闻列表
      topList: [],                // 置顶列表
      topBol: false,              // 下滑是显示文字的bol控制
      num: 0,                     // 下拉刷新了几条
      indexGray: null,            // 要被置灰的下标
      timer: null,                // 单次定时器
      downBol: false,             // 是否开始下拉加载
      lastlen: 0,                 // 上一次请求列表长度
      lastid: 0,                  // 上一次请求最后一条id
      lasttime: 0,                 // 上一次下拉时间
      page: 1,
      scroll: null,
      loading: 0
    }
  },
  methods: {
    /**
     * @method 设置广告浏览量
     * @param {string} id 广告id
     */
    setBrowserNums (id) {
      let data = {
        ad_id: id,
        type: 0,
        uuid: this.$store.state.common.uuid
      }
      this.$api.get('AdvTOTAL', data, res => {})
    },
    // 滚动函数
    scrollFn () {
      // 该节点下所有元素
      let ads = this.$refs.muAdRooot.$el.querySelectorAll('.mutiAdList')
      if (ads && ads.length > 0) {
        for (let v of ads) {
          if (this.$utils.setObj(v, this.setBrowser) && (v.getBoundingClientRect().top - window.outerHeight < 0)) {
            this.setBrowser[v.dataset.id] = true
            console.log(this.setBrowser)
            this.setBrowserNums(v.dataset.id)
          }
        }
      }
    },
    deleteAd (index) {
      this.list.splice(index, 1)
    },
    // 加载上拉下拉组件
    loadData () {
      this.$nextTick(() => {
        if (!this.scroll) {
          if (!this.$refs.wrapper) {
            return
          }
          this.scroll = new BScroll(this.$refs.wrapper, {
            click: true,
            probeType: 3,
            pullDownRefresh: {
              threshold: 50,
              stop: 0
            },
            pullUpLoad: {
              threshold: 120,
              stop: 0
            },
            mouseWheel: {
              speed: 10,
              invert: false,
              easeTime: 300
            },
            bounceTime: 400,
            scrollbar: {
              fade: true,
              interactive: false
            }
          })
          this.scroll.on('pullingDown', () => {
            this.downList()
          })
          this.scroll.on('pullingUp', () => {
            this.upList()
          })
          this.scroll.on('scroll', (pos) => {
            this.scrollFn()
            if (pos.y < 80 && pos.y > -80) {
              this.$emit('topscroll', pos.y)
            }
          })
        } else {
          this.$nextTick(() => {
            this.scroll.refresh()
          })
        }
      })
    },
    // 获取营销、置顶列表
    getTopList () {
      let isTop = this.$utils.cache('isTop')
      if (isTop && this.$utils.getTimeStamp() > isTop * 1) {
        isTop = false
      }
      if (isTop) {
        return
      }
      if (this.columnid === 0) {
        this.$api.get('TOP', {}, (res) => {
          this.$utils.cache('isTop', this.$utils.getTodayStamp())
          this.topList = res.data || []
        })
      }
    },
    // 第一次获取新闻列表
    getList () {
      let data = {}
      this.num = 0   // 重置下拉了多少条
      data.ch_rand = this.$config.ch_rand  // 身份标识
      data.columnid = this.columnid
      data.page = this.page
      data.ad_position = 2
      let isTop = this.$utils.cache('isTop')
      if (data.columnid === 0 && (!isTop || this.$utils.getTimeStamp() > isTop * 1)) {
        data.is_first = 1
      } else if (data.columnid !== 0) {
        data.is_first = 1
      }
      let urlStr = ''
      if (data.columnid > 1) {
        urlStr = 'ARTICLELIST'
        data.channel_id = data.columnid
      } else {
        urlStr = 'PULL_UP'
      }
      this.$api.get(urlStr, data, (res) => {
        if (data.columnid > 1) {
          this.list = res.data.data || []
        } else {
          this.list = res.data || []
        }
        if (data.columnid * 1 === 0 && data.is_first === 1) {
          this.$utils.cache('isTop', this.$utils.getTodayStamp())
        }
        this.lastlen = this.list.length
        this.page++
        if (this.lastlen) {
          this.lastid = this.list[(this.lastlen - 1)].id
        }
        this.$nextTick && this.$nextTick(() => {
          this.loadData()
          if (this.scroll) {
            this.scroll.scrollTo(0, 0, 1000)
          }
        })
      })
    },
    // 上次更新点击事件
    refresh () {
      if (this.scroll) {
        this.scroll.scrollTo(0, 0, 200)
      }
      this.downList()
    },
    // 获取下拉列表
    downList () {
      this.$indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      if (this.num) {
        this.list.splice(this.num, 1)
      }
      let data = {}
      data.columnid = this.columnid
      data.lastid = this.lastid
      data.lasttime = this.lasttime
      data.lastlen = this.lastlen
      data.ad_position = 2
      data.ch_rand = this.$config.ch_rand  // 身份标识
      data.page = this.page
      let urlStr = ''
      if (data.columnid > 1) {
        urlStr = 'ARTICLELIST'
        data.channel_id = data.columnid
      } else {
        urlStr = 'PULL_UP'
      }
      this.$api.get(urlStr, data, (res) => {
        clearTimeout(this.timer)
        this.lasttime = res.time
        let lists
        if (data.columnid > 1) {
          lists = res.data.data || []
        } else {
          lists = res.data || []
        }
        this.page++
        this.lastlen = lists.length
        if (this.lastlen) {
          this.lastid = lists[(this.lastlen - 1)].id
        }
        this.num = lists.length
        // 刷新后重启上拉加载
        this.scroll.openPullUp()
        if (this.num) {
          this.list = [...lists, {type: 999, id: 0}, ...this.list]
        } else {
          this.list = [...lists, ...this.list]
        }
        this.topList = []
        this.topBol = true
        this.$nextTick(() => {
          this.scroll.finishPullDown()
          // 恢复双击状态
          this.$store.commit('common_set_dblickLoading', 0)
          this.scroll.refresh()
          this.$indicator.close()
          this.timer = setTimeout(() => {
            this.topBol = false
          }, 2000)
        })
      }, () => {
        this.scroll.finishPullDown()
        this.scroll.refresh()
        this.$indicator.close()
      }, () => {
        // 第三个是断网或网络异常时的请求
        this.scroll.finishPullDown()
        this.scroll.refresh()
        this.$indicator.close()
      })
    },
    // 获取上拉列表
    upList () {
      if (this.downBol) {  // 防止鼠标的多次加载
        return
      }
      this.loading = 1
      this.$emit('downFn', true)
      this.downBol = true
      let data = {}
      data.columnid = this.columnid
      data.lastid = this.lastid
      data.lastlen = this.lastlen
      data.ch_rand = this.$config.ch_rand  // 身份标识
      data.page = this.page
      let urlStr = ''
      if (data.columnid > 1) {
        urlStr = 'ARTICLELIST'
        data.channel_id = data.columnid
      } else {
        urlStr = 'PULL_UP'
      }
      this.$api.get(urlStr, data, (res) => {
        let list
        if (data.columnid > 1) {
          list = res.data.data || []
        } else {
          list = res.data || []
        }
        this.page++
        this.lastlen = list.length
        if (this.lastlen) {
          this.lastid = list[(this.lastlen - 1)].id
        }
        this.page++
        this.loading = 0
        this.list = [...this.list, ...list]
        // 当没有数据后关闭上拉加载
        if (this.list.length === 0) {
          this.scroll.closePullUp()
        }
        this.$nextTick(() => {
          this.scroll.finishPullUp()
          this.scroll.refresh()
          this.$emit('downFn', false)
          this.downBol = false
        })
      }, () => {
        this.downBol = false
      })
      this.$nextTick(() => {
        this.scroll.finishPullUp()
        this.scroll.refresh()
      })
    },
    // 初始化 置顶放在列表 is_frist并且isTop缓存不存在
    initData () {
      this.getList()     // 获取新闻列表
    },
    // 更新需要置灰的下标
    upDate (index) {
      this.indexGray = index
    },
    // 刷新列表是否关注
    isAttention () {
      let addArr = []
      let cancelArr = []
      if (this.$utils.cache('add_attention')) {
        addArr = JSON.parse(this.$utils.cache('add_attention')) || []         // 增加的数组
      }
      if (this.$utils.cache('cancel_attention')) {
        cancelArr = JSON.parse(this.$utils.cache('cancel_attention'))   // 删除的数组
      }
      for (let i in addArr) {
        for (let k in this.list) {
          if (addArr[i] === this.list[k].mid) {
            this.list[k].is_follow = 1
          }
        }
      }
      for (let i in cancelArr) {
        for (let k in this.list) {
          if (cancelArr[i] === this.list[k].mid) {
            this.list[k].is_follow = 1
          }
        }
      }
      this.$utils.cache('add_attention', null)
      this.$utils.cache('cancel_attention', null)
    }
  },
  activated () {
    this.isAttention() // 刷新列表是否关注
    this.$nextTick(() => {
      this.scroll && typeof this.scroll.refresh === 'function' && this.scroll.refresh()
    })
    if (this.indexGray) {
      if (this.topList.length > 0) {
        if ((this.topList.length - 1) < this.indexGray) {
          this.$set(this.list[this.indexGray - this.topList.length], 'bol', true)
        } else {
          this.$set(this.topList[this.indexGray], 'bol', true)
        }
      } else {
        this.$set(this.list[this.indexGray], 'bol', true)
      }
    }
  },
  mounted () {
    this.loadData()
    if (this.columnid === 0) {
      this.initData()
    } else {
      this.timer = setTimeout(() => {
        this.initData()
      }, 400)
    }
  },
  destroyed () {
    if (this.timer) {
      this.timer = null
    }
  },
  components: {
    NewsList
  }
}
</script>
<style lang="scss" scoped>
#commonList{
  width: 100%;
  .wrapper{
    width: 100%;
    height: calc(100vh - 260px);
    box-sizing: border-box;
    &.isBrowser {
      height: calc(100vh - 400px);
    }
    .topTxt{
      background: #FCEAC5;
      height: 66px;
      overflow: hidden;
      color: #fa0;
    }
  }
}
</style>



