<template>
  <div id="selectCity" style="z-index: 120">
    <div class="nav">
      <div class="imgDiv" @click="backToIndex()">
        <img src="../../assets/img/black-left.png" alt="">
      </div>
      当前城市 - {{user_city?user_city:city}}
    </div>
    <mt-index-list class="list">
      <div class="top">您当前可能在</div>
      <mt-index-section index="#">
        <mt-cell :title="city?city:'未知'" @click.native="cityBtn(city)"></mt-cell>
      </mt-index-section>
      <mt-index-section
      :index="item.type"
      v-for="item in cityArr"
      :key="item.type">
        <mt-cell
        :title="itemSon[0]"
        @click.native="cityBtn(itemSon[0])"
        v-for="itemSon in item.arr"
        :key="itemSon[0]"></mt-cell>
      </mt-index-section>
    </mt-index-list>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  name: 'selectCity',
  computed: mapState({
    commonState: state => state.common
  }),
  data () {
    return {
      cityArr: [],            // 城市列表
      user_city: '',      // 用户手动选择信息
      city: ''            // ip所在城市
    }
  },
  methods: {
    ...mapMutations(['comment_set_city']),
    backToIndex () {
      // this.$emit('closeCity')
      this.$router.go(-1)
    },
    cityBtn (city) {
      if (this.commonState.login_bol) {
        this.$api.post('CITY_SET', {city: city}, (res) => {
          this.$utils.cache('user_city', city)
          this.comment_set_city(city)
          this.backToIndex()
        })
      } else {
        this.$utils.cache('user_city', city)
        this.backToIndex()
      }
    },
    getCity () {
      this.$api.getNoCode('GETCITY', {}, (res) => {
        this.city = this.$utils.exceptCity(res.data)
      })
    },
    getUserCity () {
      this.user_city = this.$utils.cache('user_city') || ''
    },
    getCityArr () {
      if (this.$utils.cache('cityArr')) {
        this.cityArr = JSON.parse(this.$utils.cache('cityArr'))
      } else {
        this.cityArr = require('../../assets/js/city.js').default
        this.$utils.cache('cityArr', JSON.stringify(this.cityArr))
      }
    },
    initData () {
      this.getCityArr()
      this.getCity()
      this.getUserCity()
    }
  },
  mounted () {
    this.initData()
  }
}
</script>

<style lang="scss">
#selectCity{
  // position: relative;
  background: #fff;
  transition: all 0s ease!important;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
  width: 100%;
  height: 100vh;
  .nav{
    line-height: 90px;
    text-align: center;
    font-size: 34px;
    position: relative;
    .imgDiv{
      position: absolute;
      width: 18px;
      height: 32px;
      padding: 29px 29px 0 29px;
      line-height: 32px;
      img{
        width: 100%;
        height: 100%;
      }
    }
  }
  .list{
    -webkit-overflow-scrolling: touch;
    .mint-indexlist-content{
      position: relative;
      height: calc(100vh - 90px);
      .top{
        position: absolute;
        top: 0;
        padding-left: 40px;
        font-size: 28px;
        line-height: 52px;
        width: 50%;
        color: #666;
        background: #fafafa;
      }
    }
  }
  .mint-indexlist-navitem{
    font-size: 30px;
    line-height: 38px;
  }
  .mint-cell-title{
    font-size: 34px;
    padding: 28px;
  }
  .mint-indexsection-index{
    font-size: 28px;
    padding-left: 40px;
    color: #666;
  }
}
</style>



