<template>
  <div id="bigGifts" class="scrollTouch" @click="setShowGift()">
    <div class="carImg iconSize">
      <img src="../../../assets/img/carImg.png" alt="车图片">
    </div>
    <div class="flex-row posC">点击领豪车</div>
  </div>
</template>
<script>
export default {
  data () {
    return {

    }
  },
  methods: {
    setShowGift () {
      this.$store.commit('setShowShare', true)
      this.$store.commit('set_shareType', 9)
    }
  }
}
</script>

<style lang="scss" scoped>

#bigGifts{
  width: 172px;
  height: 159px;
  background: #D01F1F;
  box-sizing: border-box;
  border-radius: 15px;
  position: fixed;
  z-index: 10;
  right: 30px;
  bottom: 130px;
  .carImg{
    width: 163px;
    height: 92px;
    margin: 20px 0;
  }
  .posC{
    position: absolute;
    white-space: nowrap;
    font-size: 20px;
    color: #fff;
    bottom: 0;
    border-radius: 0 0 15px 15px;
    background: rgba($color: #8E0000, $alpha: .85);
    height: 40px;
    left: 0;
    right: 0;
    transform: translate(0, 0)
  }
}
</style>



