<template>
  <div id="localList">
    <div class="wrapper" ref="wrapper">
      <div class="bg-fff" ref="bg_fff">
        <transition name="topTxt">
          <div class="city border-1px" v-if="selectCityBol" @click="selectCityFn">
            点击选择其他城市
            <div class="imgDiv">
              <img src="../../assets/img/position-icon.png" alt="">
            </div>
          </div>
        </transition>
        <transition name="topTxt">
          <div class="text-center lh66 topTxt" v-if="topBol">
            {{num!==0?"为您推荐"+num+"条更新":"暂无更新，请休息会再来~"}}
          </div>
        </transition>
        <ul class="list bg-fff">
          <li
          class="border-1px"
          :class="{'lastTime':item.type===999}"
          v-for="(item,index) in list"
          @click="toDetails(item.article_url, item.read_count, item)"
          :key="index">
          <div class="clear lastBox" v-if="item.type === 999" @click.stop="refresh">
            <div class="fl">上次您看到这里</div>
            <div class="fl">点击刷新</div>
            <div class="fl"><img src="../../assets/img/icon-refresh.png" alt=""></div>
          </div>
            <!-- 无图 -->
            <div class="img-zero minHeight" v-if="!item.image_list&&item.type !== 999">
              <div class="title" :class="{'coloraaa':(item.bol&&gray)}">
                {{item.title}}
              </div>
              <div class="clear mT30">
                <div class="fl address ellipsis">{{item.user_info.name}}</div>
                <div class="fl num">阅读 {{$utils.thousandRead(item.read_count)}}</div>
                <div class="fl time">{{$utils.timeFormat(item.publish_time*1000)}}</div>
              </div>
            </div>
            <!-- 三图 -->
            <div class="img-three minHeight" v-if="item.image_list&&item.type !== 999">
              <div class="title" :class="{'coloraaa':(item.bol&&gray)}">
                {{item.title}}
              </div>
              <div class="clear imgList">
                <div class="fl imgDiv imgPosition"
                v-for="(itemSon, index) in item.image_list"
                :key="index"
                :style="{background: 'url('+itemSon.url+') no-repeat'}">
                </div>
              </div>
              <div class="clear mT30 minHeight">
                <div class="fl address ellipsis">{{item.user_info.name}}</div>
                <div class="fl num">阅读 {{$utils.thousandRead(item.read_count)}}</div>
                <div class="fl time">{{$utils.timeFormat(item.publish_time*1000)}}</div>
              </div>
            </div>
          </li>
        </ul>
        <list-no-data v-if="!list || list.length === 0"></list-no-data>
      </div>
    </div>
    
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import {mapState} from 'vuex'
import listNoData from '@/components/common/list-no-data'

export default {
  name: 'localList',
  props: {
    columnid: {
      type: Number,
      default: 0
    },
    isChange: {
      type: Number,
      default: 0
    }
  },
  components: {
    listNoData
  },
  computed: mapState({
    comment: state => state.comment
  }),
  data () {
    return {
      list: [],                   // 新闻列表
      topBol: false,              // 下滑是显示文字的bol控制
      num: 0,                     // 下拉刷新了几条
      indexGray: null,            // 要被置灰的下标
      timer: null,                // 单次定时器
      downBol: false,             // 是否开始下拉加载
      user_city: '',              // 用户手动选择信息
      city: '',                   // ip所在城市
      selectCityBol: true,       // 选则城市
      selectCitytimer: null,      // 选则城市
      page: 1,                    // 列表参数
      lasttime: 0                 // 上一次下拉时间
    }
  },
  methods: {
    // 去选择城市
    selectCityFn () {
      // this.$emit('showCity')
      this.$indicator.open({
        text: '请稍等...',
        spinnerType: 'fading-circle'
      })
      setTimeout(() => {
        this.$router.push({path: '/SelectCity'})
      }, 100)
    },
    // 跳转页面
    toDetails (url, read, item) {
      url = item.display_url ? item.display_url : item.article_url
      url = url[url.length - 1] === '/' ? url.substr(0, url.length - 1) : url
      let temp = url.match(/\/?(\w+\d+)\.html$|\/?(\d+)$/)
      if (temp) {
        if (temp[1]) {
          url = temp[1]
        } else if (temp[2]) {
          url = temp[2]
        }
      } else {
        url = null
      }
      if (url) {
        this.$utils.cache('read_count', read)
        this.$router.push({path: '/LocalDetails/' + url})
      } else {
        this.$router.push('/notFound')
      }
    },
    // 加载上拉下拉组件
    loadData () {
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.wrapper, {
            click: true,
            probeType: 3,
            pullDownRefresh: {
              threshold: 50,
              stop: 0
            },
            pullUpLoad: true,
            mouseWheel: {
              speed: 10,
              invert: false,
              easeTime: 300
            },
            bounceTime: 400,
            scrollbar: {
              fade: true,
              interactive: false
            }
          })
          this.scroll.on('pullingDown', () => {
            this.downList()
          })
          this.scroll.on('pullingUp', () => {
            this.upList()
          })
          this.scroll.on('scroll', (pos) => {
            if (pos.y < 80 && pos.y > -80) {
              this.$emit('topscroll', pos.y)
            }
          })
        } else {
          this.$nextTick(() => {
            this.scroll.refresh()
          })
        }
      })
    },
    // 第一次获取新闻列表
    getList () {
      clearTimeout(this.selectCitytimer)
      let data = {category: 'news_local', city: this.city, user_city: this.user_city}
      this.$api.getNoCode('LOCAL_LISTS', data, (res) => {
        this.page++
        let dataJson = res
        let arr = []
        for (let i in dataJson.data) {
          if (!JSON.parse(dataJson.data[i].content).display_url) {
            continue
          }
          if (JSON.parse(dataJson.data[i].content).display_url.indexOf('mp.qq.com') === -1 && JSON.parse(dataJson.data[i].content).display_url.indexOf('http') > -1) {
            arr.push(JSON.parse(dataJson.data[i].content))
          }
        }
        this.list = arr
        this.$nextTick(() => {
          this.loadData()
          this.selectCitytimer = setTimeout(() => {
            this.selectCityBol = false
          }, 10000)
          if (this.scroll) {
            this.scroll.scrollTo(0, 0, 1000)
          }
        })
      })
      // this.$api.otherPost('/api/news/feed/v88/', data, (res) => {
      // })
    },
    // 上次更新点击事件
    refresh () {
      if (this.scroll) {
        this.scroll.scrollTo(0, 0, 200)
      }
      this.downList()
    },
    // 获取下拉列表
    downList () {
      this.$indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      // 城市选择，下拉时显示出来
      clearTimeout(this.selectCitytimer)
      this.selectCityBol = true
      this.selectCitytimer = setTimeout(() => {
        this.selectCityBol = false
      }, 10000)
      if (this.num) {
        this.list.splice(this.num, 1)
      }
      let data = {category: 'news_local', page: this.page, city: this.city, user_city: this.user_city}
      this.$api.getNoCode('LOCAL_LISTS', data, (res) => {
        this.page++
        let dataJson = res
        let arr = []
        for (let i in dataJson.data) {
          if (!JSON.parse(dataJson.data[i].content).display_url) {
            continue
          }
          if (JSON.parse(dataJson.data[i].content).display_url.indexOf('toutiao.com') > -1) {
            arr.push(JSON.parse(dataJson.data[i].content))
          }
        }
        clearTimeout(this.timer)
        this.num = arr.length
        this.scroll.openPullUp()
        if (this.num) {
          let lists = [...arr, {type: 999, id: 0}, ...this.list]
          this.list = lists
        } else {
          this.list = [...arr, ...this.list]
        }
        this.topBol = true
        this.$nextTick(() => {
          this.scroll.finishPullDown()
          this.scroll.refresh()
          this.$indicator.close()
          this.timer = setTimeout(() => {
            this.topBol = false
          }, 2000)
        })
      })
    },
    // 获取上拉列表
    upList () {
      if (this.downBol) {  // 防止鼠标的多次加载
        return
      }
      this.$emit('downFn', true)
      this.downBol = true
      let data = {category: 'news_local', page: this.page, city: this.city, user_city: this.user_city}
      this.$api.getNoCode('LOCAL_LISTS', data, (res) => {
        this.page++
        let dataJson = res
        let arr = []
        for (let i in dataJson.data) {
          if (!JSON.parse(dataJson.data[i].content).display_url) {
            continue
          }
          if (JSON.parse(dataJson.data[i].content).display_url.indexOf('toutiao.com') > -1) {
            arr.push(JSON.parse(dataJson.data[i].content))
          }
        }
        this.list = [...this.list, ...arr]
        // 当没有数据后关闭上拉加载
        if (this.list.length === 0) {
          this.scroll.closePullUp()
        }
        this.$nextTick(() => {
          this.scroll.finishPullUp()
          this.scroll.refresh()
          this.$emit('downFn', false)
          this.downBol = false
        })
      }, () => {
        this.downBol = false
      })
      this.$nextTick(() => {
        this.scroll.finishPullUp()
        this.scroll.refresh()
      })
    },
    // 获取ip城市
    getCity () {
      this.user_city = this.$utils.cache('user_city') || ''    // 获取是否有选择城市
      this.$api.get('GETCITY', {}, (res) => {
        this.city = res.data
        this.getList()     // 获取新闻列表
      })
    },
    // 初始化
    initData () {
      this.getCity()       // 获取ip城市
    },
    // 更新需要置灰的下标
    upDate (index) {
      this.indexGray = index
    }
  },
  watch: {
    // 检测到城市修改时 重新刷新页面
    'comment.user_city': function () {
      if (!this.selectCityBol) {
        clearTimeout(this.selectCitytimer)
        this.selectCityBol = true
        this.selectCitytimer = setTimeout(() => {
          this.selectCityBol = false
        }, 10000)
      }
      if (this.indexGray) {
        if (this.topList.length) {
          if ((this.topList.length - 1) < this.indexGray) {
            this.$set(this.list[this.indexGray - this.topList.length], 'bol', true)
          } else {
            this.$set(this.topList[this.indexGray], 'bol', true)
          }
        } else {
          this.$set(this.list[this.indexGray], 'bol', true)
        }
      }
      this.loadData()
      if (this.user_city !== this.$utils.cache('user_city')) {
        this.user_city = this.$utils.cache('user_city')
        if (this.city) {     // 首次进来不要加载列表，防止二次加载
          this.getList()      // 获取新闻列表
        }
      }
    },
    // 检测到视图从选择城市变到本地时重新设置 BScroll
    'isChange': function () {
      this.scroll.refresh()
    }
  },
  activated () {
    if (!this.selectCityBol) {
      clearTimeout(this.selectCitytimer)
      this.selectCityBol = true
      this.selectCitytimer = setTimeout(() => {
        this.selectCityBol = false
      }, 10000)
    }
    if (this.indexGray) {
      if (this.topList.length) {
        if ((this.topList.length - 1) < this.indexGray) {
          this.$set(this.list[this.indexGray - this.topList.length], 'bol', true)
        } else {
          this.$set(this.topList[this.indexGray], 'bol', true)
        }
      } else {
        this.$set(this.list[this.indexGray], 'bol', true)
      }
    }
    this.loadData()
    if (this.user_city !== this.$utils.cache('user_city')) {
      this.user_city = this.$utils.cache('user_city')
      if (this.city) {     // 首次进来不要加载列表，防止二次加载
        this.getList()      // 获取新闻列表
      }
    }
  },
  mounted () {
    if (!this.$utils.cache('cityArr')) {
      this.cityArr = require('../../assets/js/city.js').default
      this.$utils.cache('cityArr', JSON.stringify(this.cityArr))
    }
    this.loadData()
    this.initData()
  }
}
</script>

<style lang="scss" scoped>
#localList{
  .city{
    line-height: 65px;
    height: 65px;
    font-size: 28px;
    width: 750px;
    text-align: center;
    position: relative;
    background: #fff;
    overflow: hidden;
    .imgDiv{
      position: absolute;
      width: 34px;
      height: 42px;
      right: 224px;
      transform: scale(.7);
      top: 8px;
      img{
        width: 100%;
        height: 100%;
      }
    }
  }
  .imgPosition{
    background-size: 100% !important;
    background-position-y: 50% !important;
  }
  .wrapper{
    width: 100%;
    height: calc(100vh - 252px);
    .topTxt{
      background: #FCEAC5;
      height: 66px;
      overflow: hidden;
      color: #fa0;
    }
  }
  .list{
    width: 100%;
    .lastTime{
      min-height: 0px;
      padding: 0;
      .lastBox{
        font-size: 26px;
        line-height: 36px;
        padding: 10px 0px 10px 160px;
        color: #3484b9;
        >div:nth-of-type(1){
          margin-right: 20px;
        }
        >div:nth-of-type(3){
          width: 26px;
          height: 26px;
          margin-left: 10px;
          margin-top: 3px;
          img{
            width: 100%;
            height: 100%;
          }
        }
      }
    }
    li{
      .img-big{
        .imgList{
          margin-top: 20px;
          width: 100%;
          .imgDiv{
            width: 100%;
            max-height: 360px;
            overflow: hidden;
            img{
              width: 100%;
            }
          }
        }
      }
      .img-three{
        .imgList{
          padding-top: 40px;
          .imgDiv{
            width: 225px;
            height: 140px;
            img{
              width: 100%;
              height: 100%;
            }
          }
          .imgDiv:nth-of-type(1){
            margin-right: 4px;
          }
          .imgDiv:nth-of-type(2){
            margin-right: 4px;
          }
        }
      }
      .img-one{
        .img{
          width: 200px;
          height: 124.4px;
          margin-left: 10px;
          img{
            width: 100%;
            height: 100%;
          }
        }
      }
      .title{
        font-size: 38px;
        line-height: 50px;
        max-height: 150px;
        overflow: hidden;
      }
      .address,.num{
        margin-right: 20px;
        font-size: 24px;
        line-height: 26px;
        max-width: 180px;
        color: #999;
      }
      .time{
        margin-right: 20px;
        font-size: 24px;
        line-height: 26px;
        color: #999;
      }
      .label-txt{
        font-size: 20px;
        line-height: 26px;
        margin-right: 10px;
        color: #ED575E;
        box-sizing: border-box;
        // border: 1px solid #ED575E;
        border-radius: 12px;
      }
      .expired,.delete{
        color: #ED535E;
        line-height: 30px;
        padding-left: 15px;
        padding-right: 15px;
        border: #ED535E 1px solid;
        text-align: center;border-radius: 15px;
        margin-left: 10px;
      }
      .delete{
        color: #fff;
        background: #ED535E;
      }
      .label-top{
        background: url('../../assets/img/index_top.png') no-repeat;
        background-size: 100% 100%;
        width: 46px;
        height: 22px;
      }
      .label-hot{
        background: url('../../assets/img/index_hot.png') no-repeat;
        background-size: 100% 100%;
        width: 46px;
        height: 22px;
      }
      padding: 32px 0;
      margin: 0 30px;
      min-height: 84px;
      -webkit-transition: all 1s ease-in-out;
    }
  }
}
</style>



