<template>
  <div id="smallVideoList">
    <div class="wrapper" ref="wrapper" id="wrapper" :class="{isBrowser: $config.source_style < 6}">
      <div class="bg-fff" ref="bg_fff">
        <transition name="topTxt">
          <div class="text-center lh66 topTxt" v-if="topBol">
            {{num!==0?"为您推荐"+num+"条更新":"暂无更新，请休息会再来~"}}
          </div>
        </transition>
        <ul class="list clear">
          <!-- v-lazy:background-image="'https:' + item.video_img" -->
          <li class="fl imgPosition"
          v-for="(item, index) in list" v-if="item.type !== 999"
          @click="toVideoDetail(item, index)"
          :style="{'background':'#000 url('+'https:' + $utils.exceptWebp(item.video_img) +')'}"
          :key="index">
            <!-- <img v-lazy.container="item.video_img" alt=""> -->
            <div class="close" @click.stop="cancel(index)">
              <div class="iconSize flex-row">
                <img src="../../assets/img/video-close.png" alt="">
              </div>
            </div>
            <div class='list_bottom'>
              <div class="listBottom">
                <div class="title">{{item.desc}}</div>
                <div class="clear number">
                  <div class="fl">
                    <img src="../../assets/img/video-play.png" alt="">
                  </div>
                  <div class="fl">{{item.statistics?$utils.thousandRead(item.statistics.play, 1):0}}播放</div>
                  <div class="fr">{{item.statistics?$utils.thousandRead(item.statistics.zan, 1):0}}赞</div>
                </div>
              </div>
            </div>
          </li>
          <div class="clear"></div>
          <list-loading class="list-loading" :loading="loading"></list-loading>
        </ul>
        <list-no-data v-if="!list || list.length === 0"></list-no-data>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import { mapMutations } from 'vuex'
import listNoData from '@/components/common/list-no-data'
import ListLoading from '@/components/movie/common/list-loading'
export default {
  components: {
    listNoData,
    ListLoading
  },
  name: 'smallVideoList',
  data () {
    return {
      list: [],                   // 新闻列表
      topList: [],                // 置顶列表
      topBol: false,              // 下滑是显示文字的bol控制
      num: 0,                     // 下拉刷新了几条
      indexGray: null,            // 要被置灰的下标
      timer: null,                // 单次定时器
      downBol: false,             // 是否开始下拉加载
      lastlen: 0,                 // 上一次请求列表长度
      lastid: 0,                  // 上一次请求最后一条id
      page: 1,                    // 列表参数
      lasttime: 0,                 // 上一次下拉时间
      loading: 0
    }
  },
  methods: {
    ...mapMutations([
      'comment_get_small_videolist',
      'comment_get_small_video',
      'comment_set_small_index'
    ]),
    toVideoDetail (item, index) {
      this.comment_get_small_video(item)
      this.comment_set_small_index(index)
      this.$router.push(`/samllvideodetail/${item.short_id}`)
    },
    // ×掉不要的视屏
    cancel (index) {
      let data = {}
      data.tag = this.list[index].tag
      this.$api.get('SMALL_CANCEL', data, (res) => {
        this.list.splice(index, 1)
        if (this.list.length === 6) {
          this.upList() // 还剩6条时强行加载数据
        }
      })
    },
    // 加载上拉下拉组件
    loadData () {
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.wrapper, {
            click: true,
            probeType: 3,
            pullDownRefresh: {
              threshold: 50,
              stop: 0
            },
            pullUpLoad: true,
            mouseWheel: {
              speed: 10,
              invert: false,
              easeTime: 300
            },
            bounceTime: 200,
            scrollbar: {
              fade: true,
              interactive: false
            }
          })
          this.scroll.on('pullingDown', () => {
            this.downList()
          })
          this.scroll.on('pullingUp', () => {
            this.upList()
          })
          this.scroll.on('scroll', (pos) => {
            if (pos.y < 80 && pos.y > -80) {
              this.$emit('topscroll', pos.y)
            }
          })
        } else {
          this.$nextTick(() => {
            this.scroll.refresh()
          })
        }
      })
    },
    // 第一次获取小视频
    getList () {
      this.num = 0   // 重置下拉了多少条
      this.page = 1
      let data = {}
      data.page = this.page
      data.ch_rand = this.$config.ch_rand  // 身份标识
      this.$api.getNoCode('SMALL_VIDEO', data, (res) => {
        for (let v of res) {
          v.statistics.play = v.statistics.zan + v.statistics.share + v.statistics.comment
        }
        this.page++
        this.list = res || []
        this.comment_get_small_videolist(this.list)
        this.$nextTick(() => {
          this.loadData()
          if (this.scroll) {
            this.scroll.scrollTo(0, 0, 1000)
          }
        })
      })
    },
    // 上次更新点击事件
    refresh () {
      if (this.scroll) {
        this.scroll.scrollTo(0, 0, 200)
      }
      this.downList()
    },
    // 获取下拉列表
    downList () {
      if (this.num) {
        this.list.splice(this.num, 1)
      }
      let data = {}
      data.ch_rand = this.$config.ch_rand  // 身份标识
      data.page = this.page
      this.$api.getNoCode('SMALL_VIDEO', data, (res) => {
        this.page++
        clearTimeout(this.timer)
        this.lasttime = res.time
        for (let v of res) {
          v.statistics.play = v.statistics.zan + v.statistics.share + v.statistics.comment
        }
        let lists = res || []
        this.num = lists.length
        // 刷新后重启上拉加载
        this.scroll.openPullUp()
        if (this.num) {
          this.list = [...lists, ...this.list]
          // this.list = [...lists, {type: 999, id: 0}, ...this.list]
        } else {
          this.list = [...lists, ...this.list]
        }
        this.comment_get_small_videolist(this.list)
        this.topList = []
        this.topBol = true
        this.$nextTick(() => {
          this.$store.commit('common_set_dblickLoading', 0)
          this.scroll.finishPullDown()
          this.scroll.refresh()
          this.timer = setTimeout(() => {
            this.topBol = false
          }, 2000)
        })
      }, () => {
        this.scroll.finishPullDown()
        this.scroll.refresh()
        this.$indicator.close()
      }, () => {
        // 第三个是断网或网络异常时的请求
        this.scroll.finishPullDown()
        this.scroll.refresh()
        this.$indicator.close()
      })
    },
    // 获取上拉列表
    upList () {
      if (this.downBol) {  // 防止鼠标的多次加载
        return
      }
      this.loading = 1
      this.$emit('downFn', true)
      this.downBol = true
      let data = {}
      data.page = this.page
      data.ch_rand = this.$config.ch_rand  // 身份标识
      this.$api.getNoCode('SMALL_VIDEO', data, (res) => {
        this.page++
        for (let v of res) {
          v.statistics.play = v.statistics.zan + v.statistics.share + v.statistics.comment
        }
        let list = res || []
        this.list = [...this.list, ...list]
        this.comment_get_small_videolist(this.list)
        this.loading = 0
        // 当没有数据后关闭上拉加载
        if (this.list.length === 0) {
          this.scroll.closePullUp()
        }
        this.$nextTick(() => {
          this.scroll.finishPullUp()
          this.scroll.refresh()
          this.$emit('downFn', false)
          this.downBol = false
        })
      }, () => {
        this.downBol = false
      })
      this.$nextTick(() => {
        this.scroll.finishPullUp()
        this.scroll.refresh()
      })
    },
    // 初始化
    initData () {
      this.getList()     // 获取新闻列表
    }
  },
  activated () {
    if (this.scroll && typeof this.scroll.refresh === 'function') {
      this.scroll.refresh()
    }
  },
  mounted () {
    this.loadData()
    this.timer = setTimeout(() => {
      this.initData()
    }, 1000)
  },
  deactivated () {
    if (this.timer) {
      clearTimeout()
    }
  },
  destroyed () {
    if (this.timer) {
      clearTimeout()
    }
  },
  computed: {
    commonState () {
      return this.$store.state.common
    }
  },
  watch: {
    'commonState.dbClick' () {
      console.log(this.commonState.columnid, 1231)
      if (this.commonState.columnid * 1 === 50) {
        this.downList()
      }
    }
  }
}
</script>

<style lang="scss" scoped>
#smallVideoList{
  width: 100%;
  .imgPosition{
    background-repeat: no-repeat !important;
    background-size: 100% !important;
    background-position-y: 50% !important;
  }
  .list{
    width: 100%;
    padding-bottom: 60px;
    // li[lazy=loading] {
    //   background-size: 30% !important;
    //   background-position-x: 50% !important;
    // }
    li{
      width: 372px;
      height: 655px;
      background: rgba(0, 0, 0, 1);
      margin-bottom: 2px;
      transition: all 0.5s ease-in;
      color: #fff;
      font-size: 34px;
      position: relative;
      .list_bottom{
        position:absolute;
        bottom: 0;
        left:0;
        right:0;
        // background: url('../../assets/img/transparent_bg_y.png');
        background:url('../../assets/img/transparent_bg_y.png');
        height: 100%;
        background-size:100% 100%;
        .listBottom{
          position:absolute;
          bottom: 0;
          left:0;
          right:0;
        }
      }
      .number{
        // position: absolute;
        padding: 0 18px 15px;
        // bottom: 15px;
        line-height: 38px;
        font-size: 24px;
        // left: 0;
        width: 100%;
        box-sizing: border-box;
        >div:nth-of-type(1){
          width: 22px;
          height: 28px;
          margin-right: 10px;
        }
      }
      .title{
        line-height: 38px;
        height: 72px;
        margin: 0 18px 20px;
        // 多行溢出省略号
        overflow : hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        /*! autoprefixer: off */
        -webkit-box-orient: vertical;
        /* autoprefixer: on */
      }
      .close{
        width: 21px;
        height: 21px;
        padding: 20px;
        position: absolute;
        right: 0px;
        top: 0px;
        z-index: 3;
        .iconSize{
          width: 100%;
          height: 100%;
        }
      }
      img{
        width: 100%;
        height: 100%;
      }
    }
    >li:nth-of-type(even){
      margin-left: 1px;
    }
  }
  .wrapper{
    width: 100%;
    height: calc(100vh - 282px);
    position: relative;
    &.isBrowser {
      height: calc(100vh - 410px);
    }
    .topTxt{
      background: #FCEAC5;
      height: 66px;
      overflow: hidden;
      color: #fa0;
    }
  }
  .list-loading {
    margin-top: 20px;
  }
}
</style>



