<template>
  <div id="homeIndex"  ref="homeIndex" class="homeIndex">
    <!-- <div class="wrap" v-if="(!isShowChannel) && (!isShowCity)"> -->
    <div class="wrap">
      <div class="top colorFFF bg-FA0 fixed clear flex jusB" style="z-index: 2">
        <div class="flex-row hoz-flex-start" style="flex:1">
          <div class="img" v-if="$config.source_style < 6">
            <img src='../../assets/img/index/logo.png' alt="">
          </div>
          <div class="imgText text-left f30" :class="{isWx: $utils.isWx()}" v-if="$config.source_style < 6">
            看资讯视频，赚车子赚房子
            <div class="search-icon" v-if="$utils.isWx()" @click="toArticleSearch">
              <img src="../../assets/img/movie/index-search.png" alt="搜索">
            </div>
          </div>
          <div class="imgText searchIcon text-left f30"  @click="toArticleSearch" v-else>
            <div class="iconSize">
              <img src="../../assets/img/movie/white_search.png" alt="搜索">
            </div>
            <div class="text" :class="{isAndroid: $utils.isAndroid()}">搜索你感兴趣的内容</div>
          </div>
        </div>
        <div class="flex-row hoz-flex-end" :class="{top_right: $config.source_style >= 6}">
          <div class="imgClick f18" @click="getMinReward()" v-if="remainTime <= 0 || !this.commonState.login_bol"><!--v-if="remainTime <= 0 || !this.commonState.login_bol"-->
            <s>{{awardCount}}</s>
            <s class="f18">领取</s>
          </div>
          <div class="imgClick2" v-if="remainTime > 0 && this.commonState.login_bol" >
            <span class="f20">{{showText}}</span>
          </div>
          <!-- <div class="imgTopfx"  v-if="$config.source_style >= 6" @click="showAppShare"><img src="../../assets/img/home_top.png" alt=""></div> -->
          <div class="imgTopfx shareIcon" v-if="$config.source_style >= 6" @click="showAppShare"></div>
        </div>
      </div>
      <div class="nav wp100 lh88 f34">
        <div class="scrollLevel" ref="scrollLevel">
          <div class="clear box header_box" ref="header_box" :style="oneWidth ? `width:${navList.length * (oneWidth - 9) + 9}px`:''">
            <div class="fl text-center oDiv"
              v-for="(item,index) in navList"
              :key="item.index"
              :class="{'colorFA0' : item.id * 1 === columnid * 1}"
              @click="switchColumn(item.id, index)">
              {{item.id*1 !== 43 ? item.name : ($utils.cache('user_city') ? $utils.cache('user_city') : item.name)}}
            </div>
            <div class="fl text-center oDiv">
            </div>
          </div>
        </div>
        <div class="add" @click="addFn">
        </div>
      </div>
      <!-- 列表部分 -->
      <div class="dropDown" v-show="topscroll > 20" :class="[`dropDown${ramdomIndex}`, {dropDownBottom: topscroll>= 75}]"  :style="{'margin-top':`${columnid === 44 ? (topscroll + 45) : topscroll}` +'px','transform':'rotateZ('+ topscroll*25+'deg)','-weikit-transform':'rotateZ('+ topscroll*25+'deg)'}">
      </div>
      <div class="load" :class="{isBrowser: $config.source_style < 6}">
        <div id="videoChannel" ref="videoChannel" style="z-index: 1" class="prevent" v-if="columnid * 1 === 44 && videoNav.length > 0">
          <div class="v-video-channelbox noScrollBar prevent">
            <div v-for="(item,index) in videoNav" class="prevent" :class="activeIndex === index ? 'active':''" @click="switchVideo(item,index)" :key="index">
              {{item.ch}}
            </div>
          </div>
        </div>
        <v-touch class="abc" @swipeleft="onSwipeLeft" @swiperight="onSwipeRight" tag="div">
          <transition :name="animationName">
            <keep-alive>
               <common-list
                v-if="(item.id * 1 === columnid) && (columnid * 1 !== 44&& columnid * 1 !== 43 && columnid * 1 !== 50)"
                v-for="(item, index) in navList"
                :key="index"
                :columnid="columnid"
                class="listBox"
                @downFn="downFn"
                @topscroll="topscrollFn">
              </common-list>
            </keep-alive>
          </transition>
          <!-- 本地文章列表 -->
          <transition :name="animationName">
            <keep-alive>
              <local-list v-if="columnid==43" :isChange='changeNum' class="listBox" @downFn="downFn" @topscroll="topscrollFn" @showCity='showCity'></local-list>
            </keep-alive>
          </transition>
          <!-- 小视频列表 -->
          <transition :name="animationName">
            <keep-alive>
              <small-video-list v-if="columnid==50" class="listBox" @downFn="downFn" @topscroll="topscrollFn"></small-video-list>
            </keep-alive>
          </transition>
          <!-- 视频列表 -->
          <transition :name="animationName">
            <keep-alive>
              <IndexVideo v-if="columnid === 44" :swichId="activeIndex" @downFn="downFn" @getVideoList="getVideoList" @topscroll="topscrollFn" class="listBox"></IndexVideo>
            </keep-alive>
          </transition>
        </v-touch>
      </div>
      <!-- <div class="LoadingTxt bg-fff wp100 f30 text-center" :class="{isBrowser: $config.source_style < 6}" v-if="downBol">加载中...</div> -->
    </div>
    <download></download>
    <ChooseCity v-if="isShowCity" @closeCity="closeCity"></ChooseCity>
    <BigGifts class="big" :class="{isBrowser: $config.source_style < 6}" v-if="(!commonState.login_bol) || commonState.userInfo.achieved_gift_bag === 0 && !(share.showSuccessShare && share.shareType === 9)"></BigGifts>
    <newTitle v-show="firstSHOW" ref="clic" :newUserClick='newUserClick'></newTitle>
    <IndexShare @shareSuccess='shareSuccess' v-if="$route.path === '/'" tag='index' @getWx='getWx' :appShare='appShare'></IndexShare>
    <div class="Period-model text-center f28" v-if="jlBold">
        <div>时段奖励领取成功</div>
        <div class="jbBG"></div>
        <div>+{{coin}}金币</div>
    </div>
    <!-- 关闭路由时拦截 -->
    <float-ad :adObj="adObj" @closeAd="closeAd" v-if="commonState.showIndexAd && adObj && showAd"></float-ad>
    <ConfirmExit v-if="showConfirm" :config="{confirmText:'去看看'}" @closeConfirm='closeConfirm'></ConfirmExit>
  </div>
</template>
<script>
import NewsList from '../common/news-list'
import newTitle from './newTitle'
import commonList from '../indexList/commonList'
import localList from '../indexList/localList'
import smallVideoList from '../indexList/small-video-list'
import IndexVideo from '../indexList/videoList'
import ChooseCity from '@/components/common/select-city'
import BigGifts from '@/components/home/index_components/big_gifts'
import IndexShare from '@/components/share/IndexShare'
import ConfirmExit from '@/components/share/share_page1/exit_tip'
import { mapState, mapMutations } from 'vuex'
import download from '@/components/common/download'
import floatAd from '@/components/mutiAdList/float-ad'
// import { Indicator, Toast } from 'mint-ui'
export default {
  name: 'homeIndex',
  computed: mapState({
    commonState: state => state.common,
    navList: state => state.common.my_channel,
    share: state => state.share
  }),
  data () {
    this.user_icon = this.$utils.cache('user_icon')
    return {
      adObj: {
        type: 'indexAd',
        img: ['https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/recemend_list007.png']
      },
      showAd: true,
      newUserClick: 0,
      activeIndex: 0,
      shareParams: null,
      ramdomIndex: Math.round(17 * Math.random()),
      remainTime: 0,
      changeNum: 0,
      videoNav: [],
      oneWidth: null,
      isShowChannel: false,
      navWidth: '',
      navIndex: 0,                // 频道下标
      columnid: 0,                // 频道id（默认为推荐 columnid=0）
      popupVisible: false,        // 弹出框状态
      animationName: null,        // 滑动动画名字
      downBol: false,             // 是否开始下拉加载
      topscroll: 0,                // 下拉的距离
      isShowCity: false,           // 是否显示城市列表
      showText: '',
      golbTimeShow: true,
      jlBold: false,
      firstSHOW: true,
      coin: 0,
      awardCount: '',
      appShare: {
        count: 0
      },                     // app分享参数
      wx: {},
      showConfirm: false
    }
  },
  methods: {
    /**
     * @method 设置广告浏览量
     * @param {string} id 广告id
     */
    // 滚动函数
    scrollFn () {
      // 该节点下所有元素
      console.log(this.$refs.homeIndex)
      let ads = this.$refs.homeIndex.querySelectorAll('.mutiAdList')
      console.log(ads)
      if (ads && ads.length > 0) {
        for (let v of ads) {
          console.log(v.getBoundingClientRect().top - window.outerHeight)
          if (this.$utils.setObj(v, this.setBrowser) && (v.getBoundingClientRect().top - window.outerHeight < 0)) {
            this.setBrowser[v.dataset.id] = true
            this.setBrowserNums(v.dataset.id)
          }
        }
      }
    },
    setBrowserNums (id) {
      let data = {
        ad_id: id,
        type: 0,
        uuid: this.$store.state.common.uuid
      }
      this.$api.get('AdvTOTAL', data, res => {
        console.log(res)
      })
    },
    // 关闭广告
    closeAd () {
      // this.showAd = false
    },
    // 视频导航无法滑动
    switchVideo (item, index) {
      this.activeIndex = index
    },
    // 获取微信wx
    getWx (e) {
      this.wx = e
    },
    getVideoList (value) {
      this.videoNav = value
    },
    // 跳转到搜索页
    toArticleSearch () {
      this.$router.push('/article/search')
    },
    // 关闭弹框时做的操作
    closeConfirm ({action}) {
      let exitReward = this.$utils.cache('exitReward') ? JSON.parse(this.$utils.cache('exitReward')) : {}
      exitReward.timeStamp = this.$utils.getTodayStamp()
      exitReward['/'] = true
      this.$utils.cache('exitReward', JSON.stringify(exitReward))
      if (action === 'confirm') {
        this.$router.replace('/task')
        this.showConfirm = false
      } else if (action === 'cancel') {
        if (this.$utils.isWx()) {
          this.wx.close && this.wx.close()
        } else {
          this.showConfirm = false
          this.$router.go(-1)
        }
      }
    },
    // 微信组件
    shareSuccess (e) {

    },
    // 显示app分享
    showAppShare () {
      // name = this.$route.name
      // if (name === 'homeIndex') {
      //   let data = {
      //     btn_name: 'home_btn_share'
      //   }
      // } else if (name === 'task') {
      //   let data  = {
      //     btn_name: 'task_btn_share'
      //   }
      // }
      // this.$api.post('TOTAL_CLICKBTN', data, res => {
      // })
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      this.appShare.count++
    },
    closeCity () {
      this.changeNum++
      this.isShowCity = false
    },
    showCity () {
      this.isShowCity = true
    },
    // 是否显示上拉的提示
    downFn (bol) {
      this.downBol = bol
    },
    // 更新下拉图标的下拉距离
    topscrollFn (h) {
      this.topscroll = h
      let index  = Math.round(17 * Math.random())
      if (h === 0) {
        this.ramdomIndex = index
      }
    },
    // 检查频道是否修改，重新修改columnid
    closeChannel () {
      let flag = true
      for (let v of this.commonState.my_channel) {
        if (v.id * 1 === this.columnid * 1) {
          flag = false
        }
      }
      // 如果删除了改选项时将内容重置为上一页
      if (flag) {
        this.columnid = 0
        this.navIndex = 0
      }
    },
    // 右滑
    onSwipeRight (e) {
      // 解决视频频道滑动触发问题
      let el = e.target || e.srcElement
      if (el.className.indexOf('prevent') > -1) {
        return
      }
      if (this.navList.length === 1) {
        return
      }
      this.animationName = 'RightList'
      if (this.navIndex === 0) {
        this.navIndex = this.navList.length - 1
      } else {
        this.navIndex--
      }
      this.columnid = this.navList[this.navIndex].id * 1
      this.setTitlePotision(this.navIndex, 'right')
    },
    // 左滑
    onSwipeLeft (e) {
      e = e || event
      // 解决视频频道滑动触发问题
      let el = e.target || e.srcElement
      if (el.className.indexOf('prevent') > -1) {
        return
      }
      if (this.navList.length === 1) {
        return
      }
      this.animationName = 'leftList'
      if (this.navIndex === this.navList.length - 1) {
        this.navIndex = 0
      } else {
        this.navIndex++
      }
      this.columnid = this.navList[this.navIndex].id * 1
      this.setTitlePotision(this.navIndex, 'left')
    },
    // 自定义分类
    addFn () {
      this.$router.push({name: 'CommonChannel'})
      // this.isShowChannel = true
    },
    // 关闭频道
    closePage () {
      this.isShowChannel = false
      this.closeChannel()
    },
    // 添加减少频道
    addColumns () {
      this.popupVisible = false
    },
    // 切换频道
    switchColumn (id, index) {
      this.animationName = this.navIndex > index ? 'RightList' : 'leftList'
      this.downBol = false   // 处理BUG
      this.columnid = id * 1
      this.navIndex = index
    },
    // 获取频道列表
    getColumn () {
      this.$api.get('MY_CHANNEL', {}, (res) => {
        this.common_get_channel(res.data, 'my_channel')
      })
      this.$api.get('RECOND_CHANNEL', {}, (res) => {
        this.common_get_recondChannel(res.data, 'recond_channel')
      })
    },
    setTitlePotision (index, type) {
      let scrollEl = this.$refs.scrollLevel
      let el = this.$refs.header_box
      let elChild = el.children[0]
      let elWidth = elChild.offsetWidth
      if (type === 'left') {
        if (index >= (this.navList.length - 4)) {
          scrollEl.scrollLeft = (this.navIndex - 3) * (elWidth * 2)
        } else {
          scrollEl.scrollLeft = 0
        }
      } else if (type === 'right') {
        if (index <= 4) {
          scrollEl.scrollLeft = 0
        } else {
          scrollEl.scrollLeft = (this.navIndex - 3) * (elWidth * 2)
        }
      }
    },
    // 重新拉取数据
    getInitData () {
      if (this.commonState.login_bol) {
        // 登录了有path
        this.getColumn()
        if (location.href.indexOf('path') > -1) {
          let strPara = location.href.substring(location.href.indexOf('?') + 1, location.href.length - 2)
          strPara = this.$utils.getParams(strPara)
          let url = location.origin + '/#' + decodeURIComponent(strPara.path) + '?inviteCode=' + (strPara.inviteCode ? strPara.inviteCode : strPara.invitecode)
          if (strPara.call) {
            url += '&call=' + strPara.call
          }
          // 汽车id
          if (strPara.gid) {
            if (url.indexOf('?') > -1) {
              url = url + `&gid=${strPara.gid}`
            } else {
              url = url + `?gid=${strPara.gid}`
            }
          }
          location.href = url
        }
      } else {
        // 未登录并且不是微信
        if (!this.$utils.isWx()) {
          if (location.href.indexOf('path') > -1) {
            let strPara = location.href.substring(location.href.indexOf('?') + 1, location.href.length - 2)
            strPara = this.$utils.getParams(strPara)
            let url = location.origin + '/#' + decodeURIComponent(strPara.path) + '?inviteCode=' + (strPara.inviteCode ? strPara.inviteCode : strPara.invitecode)
            if (strPara.call) {
              url += '&call=' + strPara.call
            }
            // 汽车id
            if (strPara.gid) {
              if (url.indexOf('?') > -1) {
                url = url + `&gid=${strPara.gid}`
              } else {
                url = url + `?gid=${strPara.gid}`
              }
            }
            location.href = url
          }
        }
        if (!this.$utils.cache('my_channel')) {
          this.getColumn()
        }
        if (this.commonState.index_column) {
          this.common_index_column(false)
        }
      }
    },
    // 初始化
    initData () {
      this.oneWidth = 64
      this.getIndexAd()
    },
    // 获取阅读文章累计奖励
    getArcReward () {
      if (!this.commonState.login_bol) {
        return
      }
      this.$api.get('ARTICLE_COUNT', {}, res => {
        let shareSuccessObj = Object.assign({type: 6}, res.data)
        this.set_shareSuccessObj({
          type: 7,
          shareSuccessObj
        })
        this.set_shareType(7)
        this.setShowShare(true)
      }, res => {
        // if (res.code === 2) {
        //   res.data = {
        //     coin: 750
        //   }
        //   let shareSuccessObj = Object.assign({type: 6}, res.data)
        //   this.set_shareSuccessObj({
        //     type: 7,
        //     shareSuccessObj
        //   })
        //   this.set_shareType(7)
        //   this.setShowShare(true)
        // }
      })
    },
    // 获取领取奖励
    getMinReward () { // 点击领取 消失引导提示 弹出奖励框2秒 调取倒计时
      if (!this.commonState.login_bol) {
        this.common_login_box(true)
        return
      }
      // 触发点击点击新用户引导事件
      if (!this.$utils.cache('firstSHOW')) {
        this.newUserClick++
      }
      this.$api.get('MINUTE30_SAVE', {}, res => {
        this.awardCount = res.data.coin
        this.coin = res.data.coin
        this.jlBold = true
        setTimeout(() => {
          this.jlBold = false
        }, 2000)
        this.getGold()
      })
    },
    getGold () { // 获取倒计时此时时间
      this.$api.get('Minute30', {}, res => {
        if (res.data) {
          this.remainTime = res.data.time_remaining * 1
          this.awardCount = res.data.coin
          this.showText = this.$utils.timestampTIme(this.remainTime)
          if (this.timer) {
            clearInterval(this.timer)
          }
          this.timer = setInterval(() => {
            if (this.remainTime <= 0) {
              clearInterval(this.timer)
              return
            }
            this.remainTime--
            this.showText = this.$utils.timestampTIme(this.remainTime)
          }, 1000)
        }
      })
    },
    ...mapMutations([
      'common_login_box',
      'common_index_column',
      'common_inviteCode',
      'common_get_channel',
      'common_get_recondChannel',
      'common_set_guild',
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType',
      'common_set_columnid'
    ]),
    getIndexAd () {
      if (this.commonState.login_bol) {
        this.showAd = true
        this.adObj = null
        let data = {
          ad_position: 4
        }
        this.$api.get('GET_ONEAD', data, res => {
          this.adObj = res.data.ad_info[0]
        })
      }
    }
  },
  mounted () {
    this.initData()
    // 设置登录
    if (this.$utils.isWx()) {
      this.$utils.setLogin(this)
    }
    this.getInitData()
    this.getGold()
    // this.scrollFn()
    // window.addEventListener('scroll', this.scrollFn, this.$utils.isPassive())
  },
  // 缓存会被处发的函数
  activated () {
    this.$store.commit('common_set_imgIndex', 0)
    this.setTitlePotision(this.navIndex, 'left')
    this.closePage()
    this.shareParams = null
    this.user_icon = this.$utils.cache('user_icon')
    // 本地列表名字
    if (this.$utils.cache('firstSHOW')) { // 设置没有缓存引导
      this.firstSHOW = false
    } else {
      this.firstSHOW = true
    }
    if (this.$utils.cache('user_city')) {
      for (let i in this.navList) {
        if (this.navList[i].id === 43) {
          this.navList[i].name = this.$utils.cache('user_city')
        }
      }
    }
    if (this.$utils.cache('first_login') * 1 === 1) {   // 是否打开分类选择弹框
      this.$utils.cache('first_login', null)
    }
  },
  disactivated () {
    if (this.timer) {
      clearInterval(this.timer)
      this.timer = null
    }
  },
  destroyed () {
    if (this.timer) {
      clearInterval(this.timer)
      this.timer = null
    }
  },
  watch: {
    // 监听如果登录弹框被关闭
    'commonState.login_box' () {
      if (this.$route.path === '/') {
        if (this.commonState.login_bol) {
          this.getGold()
        }
      }
    },
    $route: function (newValue, oldValue) {
      if (newValue.path === '/') {
        this.getInitData()
        this.getGold()
        this.getIndexAd()
      }
      if (this.commonState.login_bol) {
        if (oldValue.name === 'ArticleDetails' || oldValue.name === 'samllVideoDetail' || oldValue.name === 'VideoDetail' || oldValue.name === 'LocalDetails') {
          this.getArcReward()
        }
      }
    },
    // 设置频道id，便于app.vue监听
    columnid (newValue) {
      this.common_set_columnid(newValue)
    },
    // app监听返回按键并且columnid不为0时将columnid重置为0
    'commonState.columnidChange' () {
      this.columnid = 0
      this.navIndex = 0
      this.setTitlePotision(this.navIndex, 'left')
    }
  },
  components: {
    NewsList,
    commonList,
    localList,
    smallVideoList,
    IndexVideo,
    ChooseCity,
    BigGifts,
    newTitle,
    IndexShare, // 分享组件
    download,
    ConfirmExit, // 确认退出组件
    floatAd
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" type="text/css">
// 大礼包
.big.isBrowser {
  bottom: 250px!important;
}
#homeIndex {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  // position: relative;
  transition:all 0ms ease !important;
  .hight-enter-active {
    transition: all 1s ease;
  }
  .hight-enter,.hight-leave-active{
    height: 0;
  }
  .hight-enter-active,.hight-leave{
    height: 100%;
  }
  ::-webkit-scrollbar{width:0;height:0}
  .wrap{
    width: 100%;
    height: 100vh;
    overflow-y: auto;
  }
  .top{
    height: 88px;
    left: 0;
    right: 0;
    z-index: 10;
    display: flex;
    justify-content: space-between;
    padding-left: 30px;
    .img{
      width: 35px;
      height: 34px;
      /*margin-top: 10px;*/
      margin-right: 18px;
    }
    .top_right {
      width: 200px;
    }
    .imgClick{
      height: 44px;
      width: 100px;
      min-width: 100px;
      display: flex;
      align-items: center;
      background:url("../../assets/img/home_click.png") no-repeat;
      background-size: 100% 100%;
      box-sizing: border-box;
      margin-right: 20px;
      position: relative;
      color: #D42E00;
      justify-content: flex-start;
      s {
        text-decoration:none;
        display: inline-block;
        padding-top: 6px;
        &:first-child{
          margin-right: 6px;
          width: 44px;
        }
      }
      span{
        height: 44px;
        line-height: 44px;
        width: 100px;
        display: flex;
        margin-left: 4px;
        text-align: center;
        /*justify-content: space-between;*/
        box-sizing: border-box;
        
      }
    }
    .imgClick2{
      height: 44px;
      width: 100px;
      background: url("../../assets/img/home_click2.png") no-repeat;
      background-size: 100%;
      align-items: center;
      position: relative;
      margin-right: 20px;
      display: flex;
      span{
        padding-left:44px;
        padding-top: 4px;
        color: #D42E00;
        width: 100px;
        display: flex;
        text-align: left;
        justify-content: space-between;
        box-sizing: border-box;
      }
    }
    .imgTopfx{
      width: 40px;
      height: 88px;
      position: initial;
      margin-left: 10px;
      background-size: 40px 40px;
      padding-right: 30px;
      margin-right: -10px;
      background-position: 0 25px;
    }
    .imgText{
      flex: 1;
      padding-top: 2px;
      line-height: 42px;
      height: 42px;
      display: flex;
      align-items: center;
      &.searchIcon {
        width: 496px;
        flex: initial;
        height: 60px;
        box-sizing: border-box;
        background: #E49C0B;
        border-radius: 30px;
        display: flex;
        flex-flow: row nowrap;
        justify-content: flex-start;
        align-items: center;
        font-size: 28px;
        padding-left: 38px;
        .iconSize {
          width: 28px;
          height: 28px;
          display: flex;
          align-items: center;
          margin-right: 10px;
        }
        .text {
          color: rgba($color: #fff, $alpha: .8);
          height: 34px;
          line-height: 34px;
          &.isAndroid {
            margin-top: 6px;
          }
        }
      }
      &.isWx {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .search-icon {
          width: 46px;
          height: 46px;
          margin-right: 20px;
        }
      }
    }
    img{
      width: 100%;
      height: 100%;
    }
  }
  .nav{
    position: fixed;
    top: 88px;
    background: #f4f5f7;
    height: 90px;
    z-index: 10;
    line-height: 90px;
    overflow: hidden;
    .scrollLevel{
      overflow: auto;
      -webkit-overflow-scrolling: touch;
    }
    .header_box{
      background: #f4f5f7;
      height: 90px;
      white-space: nowrap;
      overflow: scroll;
      padding-right: 160px;
      // display: flex;
    }
    .header_scroll_box {
      overflow-x: auto;
      -webkit-overflow-scrolling: touch
    }
    .oDiv{
      padding: 0 23px;
      float: none;
      display: inline-block;
      user-select: none;
    }
    .oDiv:nth-of-type(1){
      padding-left: 32px;
    }
    .oDiv:last-child{
      visibility: hidden;
    }
  }
  .add{
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    width: 94px;
    height: 90px;
    background:url('../../assets/img/rignt_line.png') no-repeat left center;
    background-size: 100%;
    &::after{
      content: '';
      display: block;
      position: absolute;
      right: 0;
      width: 31px;
      height: 31px;
      background: url('../../assets/img/icon_add.png') no-repeat left top;
      background-size: 100%;
      right: 30px;
      top: 50%;
      transform: translateY(-50%);
      -webkit-transform: translate(0, -50%)
    }
  }
  .load{
    // position: absolute;
    height: calc(100% - 1px);
    padding-bottom: 100px;
    padding-top: 178px;
    overflow: hidden;
    background: #fff;
    box-sizing: border-box;
    overflow: hidden;
    &.isBrowser {
      height: calc(100% - 230px);
    }
    #videoChannel{
      height: 90px;
      box-sizing: border-box;
      background: #fff;
      position: fixed;
      z-index: 1;
      left: 0;
      right: 0;
      width: 750px;
      line-height: 90px;
      overflow-y: hidden;
      overflow-x: auto;
      -webkit-overflow-scrolling: touch;
      .v-video-channelbox{
        white-space: nowrap;
        padding: 0 12px;
        width: auto;
        &>div{
          display: inline-block;
          font-size: 32px;
          padding: 0 10px;
          margin: 0 10px;
          background:#fff;
          height: 40px;
          line-height: 40px;
        }
        &>div.active{
          background: #b2b2b2;
          color: #fff;
          border-radius: 5px;
        }
      
      }
    }
  }
  .LoadingTxt{
    position: fixed;
    bottom: 100px;
    line-height: 66px;
    color: #999;
    &.isBrowser {
      bottom: 220px;
    }
  }
  .dropDown{
    position: absolute;
    background: #f00;
    top: 88px;
    z-index: 0;
    width: 50px;
    height:50px;
    background: url('../../assets/img/down-top.png') no-repeat;
    background-size:500px;
    border-radius: 50%;
    left: 50%;
    margin-left: -30px;
    transform-origin: center;
    -webkit-transform-origin: center;
    background-position: -252.5px -178.5px
    /*animation: mRotate 160ms infinite;*/
  }
  .dropDownBottom {
    transform: rotateZ(0deg)!important;
    -webkit-transform: rotateZ(0deg)!important;;
  }
  @keyframes mRotate {
    from {
      transform: rotate(0);
      -webkit-transform: rotate(0)
    }
    to {
      transform: rotate(360deg);
      -webkit-transform: rotate(360deg)
    }
  }
  .dropDown0{background-position: -19.5px -18.5px}
  .dropDown1{background-position: -97.7px -18.5px}
  .dropDown2{background-position: -173.5px -20.5px}
  .dropDown3{background-position: -252px -18.5px}
  .dropDown4{background-position: -333.5px -19.5px}
  .dropDown5{background-position: -409.5px -18.5px}

  .dropDown6{background-position: -20px -104.5px}
  .dropDown7{background-position: -98px -104px}
  .dropDown8{background-position: -177px -104px}
  .dropDown9{background-position: -252.5px -104.5px}
  .dropDown10{background-position: -332.5px -103.8px}
  .dropDown11{background-position: -411.5px -104.5px}

  .dropDown12{background-position: -20px -177px}
  .dropDown13{background-position: -97.5px -178.5px}
  .dropDown14{background-position: -177px -179.5px}
  .dropDown15{background-position: -252.5px -178.5px}
  .dropDown16{background-position: -332.5px -177.5px}
  .dropDown17{background-position: -409.5px -178px}
  .abc{
    position: relative;
    .listBox{
      position: absolute;
      left: 0;
      top: 0;
    }
  }
}
.Period-model {
  position: fixed;
  left: 50%;
  top: 50%;
  width: 250px;
  height: 320px;
  background: rgba(0,0,0,0.65);
  color: white;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  border-radius: 12px;
  z-index: 100;
  div:first-child{
    font-size: 26px;
    height: 100px;
    line-height: 100px;
  }
  div:last-child{
    line-height: 60px;
    font-size: 36px;
    color: #ffd01d;
    margin-top: -10px;
    font-weight: 900;
  }
  .jbBG{
    // width: 255px;
    // height: 142px;
    width: 194px;
    height: 193px;
    background: url("../../assets/img/task/taskReward.png") no-repeat;
    background-size: 100%;
    margin: -40px auto 0;
  }
}
</style>



