<template>
  <div class="index-ad maskBox" style="z-index: 10">
    <div class="img-wrapper">
      <img @click="setClickAd" class="adImage" :src="adObj.images[0]" alt="广告">
      <div class="ad_tag">广告</div>
      <div class="close" @click="closeFn"></div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    adObj: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  data () {
    return {
      isClick: false
    }
  },
  methods: {
    // 设置点击
    setClickAd () {
      this.$utils.openAd(this.adObj.link)
      if (!this.isClick) {
        this.isClick = true
        let data = {
          ad_id: this.adObj.id,
          type: 1,
          uuid: this.$store.state.common.uuid
        }
        this.$api.get('AdvTOTAL', data, res => {})
      }
    },
    // 设置浏览量
    setBrowerNums () {
      let data = {
        ad_id: this.adObj.id,
        type: 0,
        uuid: this.$store.state.common.uuid
      }
      this.$api.get('AdvTOTAL', data, res => {})
    },
    // 关闭
    closeFn () {
      this.$utils.cache('showBigGifts', JSON.stringify({
        timeStamp: this.$utils.getTodayStamp()
      }))
      this.$store.commit('common_set_indexad', false)
      this.$emit('closeAd')
    }
  },
  mounted () {
    this.setBrowerNums()
  }
}
</script>
<style lang="scss" scoped>
  .img-wrapper {
    width: 607px;
    margin-top: -100px;
    background: none;
    img {
      vertical-align: top;
    }
    .ad_tag {
      position: absolute;
      right: 28px;
      top: 24px;
      width: 50px;
      text-align: center;
      color: #fff;
      background: rgba(0, 0, 0, .5);
      border-radius: 6px;
      font-size: 16px;
      height: 30px;
      line-height: 32px;
    }
    .adImage {
      width: 100%;
    }
    .close {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: -70px;
      background: url('../../assets/img/adview/white_close.png') no-repeat center;
      width: 46px;
      height: 46px;
      background-size: 100% 100%;
    }
  }
</style>



